export default {
	data() {
		return {
			id:'',
			secret:{},
			type:'',
			time:'',
			shipname:'',
			buylist: [], //订单列表
			goodsPrice: 0.0, //商品合计价格
			sumPrice: 0.0, //用户付款价格
			freight: 0.00, //运费
			note: '', //备注
			int: 0, //抵扣积分
			deduction: 0, //抵扣价格
			recinfo: {},
			

		};
	},
	onLoad(option) {
		this.id=option.id;
		
	},
	onShow() {
		//页面显示时，加载订单信息
		uni.getStorage({
			key:'user',
			success: (res) => {
				this.secret=res.data;
				this.getdetails()
			}
		})
		
	},
	onHide() {

	},
	onBackPress() {
		//页面后退时候，清除订单信息
		this.clearOrder();
	},
	filters: {
		toFixed: function(x) {
			return parseFloat(x).toFixed(2);
		}
	},
	methods: {
		getdetails(){
			let data={
				xopenid: this.secret.openid,
				order_sn: this.id,
				secret: this.secret.secret,
				timestamp: this.secret.timestamp
			}
			this.$xm.post('/Order/getOrderInfo',data,(res)=>{
				switch(res.order_state) {
				     case '6':
				        res.order_state='已删除'
				        break;
				     case '8':
				        res.order_state='已退款'
				        break;
				     default:
				       
				} 
				
				this.type=res.order_state;
				this.recinfo=res.contact;
				res.pro.map(ele=>{
					ele.pro_sn='http://img.xmvogue.com/thumb/'+ele.pro_sn+'.jpg?x-oss-process=style/300'
				})					
				this.buylist=res.pro;					
				this.time=res.order_time;
				this.shipname=res.order_ship_name;
				this.goodsPrice=res.actually_amt;					
				this.freight=res.order_ship_price;
				this.sumPrice=Number(this.goodsPrice)+Number(this.freight);
				this.deduction=res.discount_amt;					
			})
		},
		clearOrder() {
			uni.removeStorage({
				key: 'buylist',
				success: (res) => {
					this.buylist = [];
					console.log('remove buylist success');
				}
			});
		},
		toPay() {
			//商品列表
			let paymentOrder = [];
			let goodsid = [];
			let len = this.buylist.length;
			for (let i = 0; i < len; i++) {
				paymentOrder.push(this.buylist[i]);
				goodsid.push(this.buylist[i].id);
			}
			if (paymentOrder.length == 0) {
				uni.showToast({
					title: '订单信息有误，请重新购买',
					icon: 'none'
				});
				return;
			}
			//本地模拟订单提交UI效果
			uni.showLoading({
				title: '正在提交订单...'
			})
			setTimeout(() => {
				uni.setStorage({
					key: 'paymentOrder',
					data: paymentOrder,
					success: () => {
						uni.hideLoading();
						uni.redirectTo({
							url: "../pay/payment/payment?amount=" + this.sumPrice
						})
					}
				})
			}, 1000)

		},
		//选择收货地址
		selectAddress() {
			uni.navigateTo({
				url: '../user/address/address?type=select'
			})
		}
	}
}
